body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.App {
  background: white;
  min-height: 1500px;
  width: auto;
}
.logo {
  position: absolute;
  left: 20px;
  top: 20px;
  color: red;
  text-align: left;
  flex-direction: row;
  justify-content: space-between; 
}

.link {
  position: absolute;
  right: 40px;
  top: 20px;
  color: red;
  text-align: left;
  flex-direction: row;
  justify-content: space-between; 
}

.Content {
  background: white;
  align-content: center;
  justify-content: center;
  display: block;
  width: 100%;
}



.Header {
    text-align: center;
    background-color: #0099cc;
    width: auto;
    height: 1000px;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    display: flex;
    color: white;
    size: 100px;
  }
.TextBox {
    margin: 100px;
    display: flex;
    margin-top: 100px;
    width: 700px;
}

.TextBox img {
    height: 170px;
    width: 160px;
    align-self: center;
}

.Text {
    margin-left: 150px;
    text-align:  left;
    color: #505050;
    font-size: 15px;
    
}
